@import url("https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap");
body, div, p {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

.platform {
  background-color: #f5f7f8;
  /* login */ }
  .platform-wrap {
    height: 100%; }
  .platform--loginWrap {
    display: flex;
    align-items: center;
    height: 100vh; }
    .platform--loginWrap-box {
      margin: 0 auto;
      width: 498px;
      background-color: #ffffff;
      border-radius: 8px 0px 8px 8px;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.16); }
    .platform--loginWrap-content {
      padding: 40px 50px; }
      .platform--loginWrap-content .left {
        text-align: left;
        margin-bottom: 20px; }
      .platform--loginWrap-content input[type="checkbox"] + label {
        font-size: 14px;
        color: #82919b; }
      .platform--loginWrap-content input[type="checkbox"] + label span {
        margin-right: 10px; }
    .platform--loginWrap-lang {
      display: block;
      text-align: left;
      float: right;
      padding-top: 15px;
      padding-right: 15px; }
    .platform--loginWrap-logo {
      margin-top: 40px;
      margin-bottom: 58px; }
    .platform--loginWrap-logo p {
      margin-top: 12px;
      margin-bottom: 0;
      font-size: 18px;
      color: #82919b; }
    .platform--loginWrap-logo img {
      width: 190px;
      height: 108px; }
    .platform--loginWrap-logininfo {
      text-align: center;
      margin-top: 40px; }
      .platform--loginWrap-logininfo p {
        margin: 0;
        font-size: 13px;
        color: #82919b; }
      .platform--loginWrap-logininfo p:last-child {
        font-weight: 500; }
    .platform--loginWrap-loginbtn {
      width: 100%;
      height: 40px; }
    .yp-selectWrap + .platform--loginWrap--content {
      content: '';
      display: block;
      clear: both; }

