  .yp-inputWrap.width100 {
    width: 100%; }
  .yp-inputWrap {
    position: relative;
    display: inline-block; }
    .yp-inputWrap input::placeholder {
      color: #82919b;
      font-weight: 300; }
    .yp-inputWrap input {
      width: 100%;
      padding: 8px 10px;
      font-size: 14px;
      color: #182d40;
      caret-color: #f05329;
      border-radius: 3px;
      border: 1px solid #d7dcde;
      box-sizing: border-box; }
    .yp-inputWrap input:hover {
      border-color: #82919b; }
    .yp-inputWrap input:focus:not(:read-only)::placeholder {
      color: #e8e9ec; }
    .yp-inputWrap input:focus {
      outline: none;
      border-color: #182d40; }
    .yp-inputWrap input:disabled,
    .yp-inputWrap input:read-only {
      color: #82919b;
      border-color: #e8e9ec;
      background-color: #f5f7f8; }
    .yp-inputWrap input:disabled + p {
      color: #82919b; }
    .yp-inputWrap p {
      margin-top: 5px;
      margin-bottom: 12px;
      font-size: 12px;
      color: #182d40; }
    .yp-inputWraperror p {
      color: #f05329; }
    .yp-inputWraperror input {
      padding-right: 24px;
      border-color: #f05329;
      background-size: 12px 13px; }
    .yp-inputWrapsuccess input {
      padding-right: 24px;
      border-color: #182d40;
      background-size: 13px 13px; }
    .yp-inputWrap input.input-calendar {
      padding-right: 26px;
      background-size: 14px 15px; }
    .yp-inputWrap input.input-calendar:hover {
      background-size: 14px 15px; }
    .yp-inputWrap input.input-calendar:disabled {
      background-color: #f5f7f8; }
    .yp-inputWrap input.input-calendar:disabled:hover {
      background-color: #f5f7f8;
      background-position: center right 10px;
      background-size: 14px 15px; }
  .yp-checbox {
    display: none;
    padding: 0;
    box-sizing: border-box; }
    .yp-checbox + label {
      font-size: 13px;
      color: #35414c;
      cursor: pointer; }
    .yp-checbox + label span {
      display: inline-block;
      width: 20px;
      height: 20px;
      margin: 0;
      vertical-align: sub;
      background: none;
      background-size: 20px 20px;
      border: 1px solid #d7dcde;
      border-radius: 3px;
      box-sizing: border-box; }
    .yp-checbox:checked + label span {
      background: url("/static/images/radiobox_select-3x.png") no-repeat;
      background-size: 20px 20px;
      border: none; }
    .yp-checbox:disabled + label span {
      background-color: #f5f7f8; }
    .yp-checbox:checked:disabled + label span {
      background-image: url("/static/images/radiobox_select_dim-3x.png"); }
  .yp-radioWrap {
    font-size: 13px; }
    .yp-radioWrap > input {
      display: none; }
    .yp-radioWrap > span {
      display: inline-block;
      position: relative;
      width: 16px;
      height: 16px;
      margin-right: 5px;
      vertical-align: sub;
      background-color: #fff;
      border: 1px solid #d7dcde;
      border-radius: 8px; }
    .yp-radioWrap > input:checked + span:before {
      content: "";
      display: block;
      position: absolute;
      top: 3px;
      left: 3px;
      width: 8px;
      height: 8px;
      background-color: #82919b;
      border-radius: 4px; }
  .yp-selectWrap {
    display: inline-block;
    height: 36px;
    font-size: 13px;
    color: #35414c;
    box-sizing: border-box; }
    .yp-selectWrap select {
      outline: none; }
  .yp-btntype1 {
    height: 40px;
    padding: 8px 10px;
    font-size: 15px;
    font-weight: normal;
    color: white;
    box-sizing: border-box;
    border-width: 0;
    background-color: #35414c;
    border-radius: 5px;
    cursor: pointer;
    outline: none; }
    .yp-btntype1:hover {
      font-weight: 500;
      background-color: #182d40; }
  .yp-dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 26px;
    margin-top: -140px;
    margin-left: -138px;
    height: 280px;
    width: 276px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0px 7px 20px 0px rgba(0, 0, 0, 0.2);
    text-align: center; }
    .yp-dialog--alert-icon {
      min-height: 10%; }
    .yp-dialog--alert-msg {
      min-height: 40%; }
    .yp-dialog--alert-btn {
      position: absolute;
      bottom: 26px;
      width: calc(100% - (26px*2)); }
